.description {
  padding-top: 103px;
  padding-bottom: 108px;
}
.description__title {
  font-weight: bold;
  font-size: 48px;
  /*line-height: 32px;*/
  letter-spacing: -0.4px;
  color: #1652AC;
  position: relative;
}
.description__text {
  font-weight: 600;
  font-size: 17px;
  line-height: 32px;
  letter-spacing: -0.4px;
  color: #020303;
}
.description__text {
  padding: 55px 0;
  position: relative;
}
.description__text::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 227px;
  height: 7px;
  background: #EDEFF3;
}
.description__text:after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 227px;
  height: 7px;
  background: #EDEFF3;
}
.description__img2 {
  display: none;
}

@media (max-width: 768px) {
  .description {
    padding-bottom: 80px;
  }

  .description__img {
    display: none;
  }

  .description__img2 {
    display: block;
    width: 354px;
    margin: 0 auto;
  }

  .description__text::before,
.description__text::after {
    display: none;
  }

  .description__title {
    text-align: center;
    padding-bottom: 40px;
  }

  .description__title::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 32%;
    width: 227px;
    height: 7px;
    background: #EDEFF3;
  }
}